.form-programs {
  position: relative;
  padding-top: 28px;

  &::before {
    position: absolute;
    width: 320px;
    left: -22px;
    bottom: 0;
    height: 1px;
    background-color: #d9d9d9;
    content: "";
  }
}

@media (min-width: @tablet-width) {
  .form-programs {
    margin: 0;
    margin-bottom: 75px;
    padding: 0;

    &::before {
      display: none;
    }
  }

  .form-programs__list {
    padding: 25px 28px;
    border: 1px solid #e7e7e7;
  }
}

@media (min-width: @desktop-width) {
  .form-programs {
    margin: 0;
  }
}
